const validate = values =>{
    let errors = {};
    const {title} = values;
    if (!title){
        errors.title ='vui lng nhp tiu  !'
    } else if (title.trim() && title.length <5){
        errors.title = 'Tiu  phi ln hn 5 k t !'
    }
    if (!values.fromtime) {
        errors.fromtime = 'Vui lng chon mc  thi gian'
      } 
    if (!values.totime) {
        errors.totime = 'Vui lng chon mc  thi gian'
    } 
    if (!values.fromdate) {
        errors.fromdate = 'Vui lng chon mc  thi gian'
    } 
    if (!values.todate) {
        errors.todate = 'Vui lng chon mc  thi gian'
    } 
    //Validation of user anh password
    if (!values.name) {
        errors.name = 'Vui lng nhp name'
      } else if (values.name.length  < 5) {
        errors.name = 'Tn phi ln hn 5 k t !'
      }
    if (!values.email) {
        errors.email = 'Vui lng nhp Email'
      } else if (!/^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i.test(values.email)) {
        errors.email = 'y khng phi Email !'
      }
      if (!values.operator) {
        errors.operator = 'Vui lng nhp operator'
      } else if (values.operator.length  > 5 ) {
        errors.operator = 'y khng phi operator !'
      }
    if (!values.password){
        errors.password="Vui lng nhp mt khu";
    }else if ((values.password.match(/[a-z]/g) && values.password.match( 
        /[A-Z]/g)  && values.password.match( 
            /[0-9]/g) && values.password.length > 5)) {
            errors.password="";
    }else{
        errors.password="Vui lng kim tra li mt khu";
    }
    if (!values.cPassword){
        errors.cPassword="Vui lng nhp mt khu";
    }else if (values.cPassword.match(/[a-z]/g) && values.cPassword.match( 
        /[A-Z]/g) && values.cPassword.match( 
        /[0-9]/g) &&  values.cPassword.length >5 ){
            errors.cPassword="";
    }else{
        errors.cPassword="Vui lng kim tra li mt khu";
    }
    if(values.cPassword !==values.password){
        errors.cPassword="Mt khu khng khp !";
    }
    return errors;
}

export default validate;